/*nav bar*/
#app, #nav-list{
  display:flex;
  flex-direction: row;
}
#image{
  width:150px;
  height:150px;
}
#nav-head{
  width:60%;
  margin-top:20px;
}
#nav-list #nav-link{
  padding:20px;
  list-style-type: none;
  font-size:22px;
  color:white;
}
#dropdown-basic{
  background-color:transparent;
  border:none;
  border-color: transparent;
  outline: none;
  font-size:22px;
  padding-top:2px;
}
#dropdown-basic:visited{
  outline:none;
  border-color:transparent;
}
#social-icons{
 padding:20px;
 font-size:20px;
 margin-top:40px;
}
#social-icons i{
  padding:20px;
  font-size:20px;
  color:white;
}
.fa-envelope{
  padding:5px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  color:rgb(231, 229, 229);
}
.row{
  margin:auto;
}
#navbar{
  display: none;
}
@media screen and (max-width:1200px){
  #social-icons{
    padding:0px;
}
}
@media screen and (max-width:600px){

#navbar{
  display: block;
  }
#nav-head, #social-icons i, .carousel{
  display: none;
}  
#social-icons-Mobile{
  display:flex;
  flex-direction: row;
  padding:150px 0px 30px 0px;
}
#social-icons-Mobile i{
  display: block;
  padding:20px;
  font-size:20px;
  color:white;
}
#nav-list{
  display:flex;
  flex-direction: column;
  padding:10px;
}
#nav-list #nav-link{
  padding:10px;
  z-index:999;
  color:white;
  font-size:22px;
}

}
