.container h3{
    text-align: center;
    font-size:60px;
    padding:0;
    margin:0;
    color:white;
}
.container p{
    text-align: center;
    font-size:38px;
    padding-top:10px;
    color:white;
}
#details{
    background: #C4C4C4;
    border: 5px solid #FFFFFF;
    box-sizing: border-box;
    border-radius: 20px;
    color:white;
    padding:10px 25px 10px 25px;
}
#carousel-item{
    margin:auto;
    margin-top:8%;
    width:80%;
}
#carousel-item>img{
    height:300px; 
    width:30%; border-radius: 30px; margin:auto;
}
#social-icons-Mobile i{
    display: none;
}
@media screen and(max-width:600px){
    #social-icons-Mobile i{
        display: block;
    }
}
@media screen and (max-width:380px){
    #details{
        margin-top:100px;
    }
}